import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"
import { mapMutations, mapState } from 'vuex'
export default {
	components: {
		uniStatusBar
	}, 
	data() {
		return {
			username:"",
			password:"",
			check:true,
			
			// 验证规则
			rules:{
				username:[
					// {
					// 	rule:/^[a-zA-Z]\w{5,17}$/,
					// 	msg:"账号必须字母开头，长度在6~18之间，只能包含字母、数字和下划线"
					// }
				],
				password:[
					// {
					// 	rule:/^.{5,20}$/,
					// 	msg:"密码长度必须为5-20个字符"
					// }
				]
			},
			
			focusClass:{
				username:false,
				password:false
			},
		}
	},
	computed: {
		...mapState({
			loginStatus: state => state.user.loginStatus,
		})
	},
	// watch: {
	// 	loginStatus(newValue, oldValue) {
	// 		if (this.loginStatus && getCurrentPages().length == 1) {
	// 			console.log(this.loginStatus + '-----')
	// 			uni.switchTab({
	// 				url: '/pages/index/index'
	// 			})
	// 		}
	// 	}
	// },
	methods: {

		mpGetUserInfo(result) {
			console.log(result)
			uni.showLoading({ title: '登录中...', mask: true })
			// 获取失败
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				uni.hideLoading()
				uni.showModal({
					title: '获取用户信息失败',
					content: '错误原因:' + result.detail.errMsg,
					showCancel: false
				})
				return
			}
			let userinfo = result.detail.userInfo
			uni.login({
				provider:'weixin',
				success: (res) => {
					console.log(res)
					// 登录
					this.$H.post('http://47.106.125.164/api.v1/micro-app/login',{
						code: res.code,
						nickName: userinfo.nickName,
						avatarUrl: userinfo.avatarUrl,
						gender: userinfo.gender,
					}).then(data => {
						if (!data.token) return
						userinfo.token = data.token
						this.login(userinfo)
						uni.hideLoading()
						uni.showToast({
							title: '登录成功',
							icon: 'none'
						});
						uni.navigateBack({
							delta: 1
						});
					})
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},

		
		
		...mapMutations(['login']),
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 表单验证
		validate(key){
			var check = true
			this.rules[key].forEach((v)=>{
				// 验证失败
				if (!(v.rule).test(this[key])) {
					uni.showToast({ title: v.msg, icon: 'none' });
					check = false
					return false
				}
			})
			return check
		},
		// 提交表单
		submit(){
			if (!this.check) {
				return uni.showToast({
					title: '请先同意XXXXX协议',
					icon: 'none'
				});
			}
			// 验证用户名
			if (!this.validate('username')) return;
			// 验证密码
			if (!this.validate('password')) return;
			
			console.log('提交成功');
			// uni.showLoading({
			// 	title: '登录中...',
			// 	mask: true
			// });
			this.$H.post('/login',{
				username:this.username,
				password:this.password
			}).then(res=>{
				console.log(res);
				// 状态存储
				this.login(res)
				uni.hideLoading()
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				});
				uni.navigateBack({
					delta: 1
				});
			})
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		}
	}
}
